/* 인기검색어 */

let goTop = 0

setInterval(() => {

    goTop += -40

    $('.lnb .hot ul').animate({
        top: `${goTop}px`
    }, 500, function(){

        goTop += 40

        $('.lnb .hot ul').animate({
            top: `${goTop}px`
        }, 0)

        const myList = document.querySelectorAll('.lnb .hot ul li')
        const parent = myList[0].parentNode

        parent.appendChild(myList[0])

        /* 뭔가를 옮기고 싶으면 조상을 찾아서 위치를 찾아야 된다 */
    })

}, 3000);
